/**************************************************************************/
/*****************************    Global    *******************************/
/**************************************************************************/

body {
	background-color: rgb(15, 17, 24);
	background-image: url(/Musomatic/static/media/hero.bd007aec.svg);
	color: rgb(236, 236, 236);
	overflow-x: hidden;
}

.loading {
	margin-top: 140px;
	padding-bottom: 400px;
	text-align: center;
}

.cursor_ptr {
	cursor: pointer;
}

.green_text {
	color: #6cc027;
	font-family: var(--Poppins);
}

a[disabled] {
	color: gray;
}

a[disabled]:hover {
	color: gray;
	animation: none;
	cursor: not-allowed;
}

a:hover {
	color: #6cc027 !important;
}

/* Scroll bar width */
::-webkit-scrollbar {
	width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
	background: rgb(13, 13, 13);
}

/* Handle */
::-webkit-scrollbar-thumb {
	border-radius: 50px;
	background: rgb(83, 194, 75);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgb(83, 194, 75);
}

:root {
	--Poppins: "Poppins", sans-serif;
	--Roboto: "Roboto", sans-serif;
}

/**************************************************************************/
/******************************    Hero    ********************************/
/**************************************************************************/

.hero-container {
	width: 100vw;
	background-image: url(/Musomatic/static/media/hero.bd007aec.svg);
	background-repeat: no-repeat;
	background-size: cover;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	padding: 50px;
	padding-top: 120px;
	padding-bottom: 80px;
}

.hero-box {
	position: relative;
	max-width: 1500px;
	font-size: 16px;
}

.hero-row {
	margin: 0 !important;
	padding: 0 !important;
}

.intro-card {
	width: 80%;
	border-radius: 20px;
	padding: 50px 0px;
	text-align: center;
	/*Frosted effect*/
	background-color: rgba(255, 255, 255, 0.1);
	-webkit-backdrop-filter: blur(5px);
	        backdrop-filter: blur(5px);
}

.intro-text {
	color: #6cc027;
	font-family: var(--Poppins);
	font-size: 1.7em;
	font-weight: 400;
}

.brand-intro {
	width: 80%;
	padding-left: 20px;
	text-align: left;
	margin-top: 60px;
}

.brand-intro h2 {
	font-family: var(--Poppins);
	font-weight: 400;
	font-size: 2.5em;
}

.brand-intro p {
	max-width: 80%;
	font-family: var(--Roboto);
	font-weight: 400;
	font-size: 1em;
}

.music-card {
	width: 80%;
	height: 150px;
	background-image: linear-gradient(to right, #458f08, #6cc027);
	border-radius: 20px;
	padding-left: 50px;
	margin-top: 75px;
	display: flex;
	overflow-y: hidden;
	overflow-x: hidden;
	align-items: center;
}

.music-card-text {
	padding-left: 15px;
	color: #232323;
	font-family: var(--Poppins);
	font-size: 1.6em;
	font-weight: 500;
}

.music-card img {
	align-self: center;
	margin: 0 55px 0 auto;
	width: 300px;
}

.about-us {
	width: 80%;
	padding-left: 20px;
	text-align: left;
	margin-top: 45px;
}

.about-us p {
	font-family: var(--Roboto);
	font-weight: 400;
	font-size: 1em;
}

p a {
	text-decoration: none;
	color: #6cc027;
}

:root {
	--Poppins: "Poppins", sans-serif;
	--Roboto: "Roboto", sans-serif;	
}

/**************************************************************************/
/*****************************    Button    *******************************/
/**************************************************************************/

.submit-btn {
    width: 100%;
    color: #6cc027;
	border: 1px solid #6cc027;
    border-radius: 30px;
	outline: none;
    background-color: #202020;
	font-family: var(--Poppins);
	font-weight: 500;
	position: relative;
	padding: 10px 30px;
	cursor: pointer;
	text-align: center;
	display: block;    
}

.submit-btn:hover {
    color: #202020;
    border: 1px solid #6cc027;
	box-shadow: 0 0 11px rgba(33, 33, 33, 0.2);
	background-color: #6cc027 !important;
}

.submit-btn span {
	cursor: pointer;
	display: inline-block;
	position: relative;
	transition: 0.5s;
}

.submit-btn i {
	position: absolute;
	opacity: 0;
	top: 0;
	right: -20px;
	transition: 0.25s;
    font-weight: 700;
	margin-top: 3px;
}

.submit-btn:hover span {
	padding-right: 25px;
}

.submit-btn:hover i {
	opacity: 1;
	right: 0;
}

.submit-btn:focus {
    color: #202020 !important;
	background-color: #6cc027 !important;
	border: 1px solid #6cc027;
	outline: none !important;
}

.submit-btn:disabled,
.submit-btn[disabled] {
	color: #202020 !important;
	background-color: #6cc027 !important;
	border: 1px solid #6cc027;
	outline: none !important;
}
